import { useState, useEffect, useRef, FormEvent, KeyboardEvent } from 'react'
import { FiPlus, FiEdit2, FiType, FiFlag, FiCalendar, FiTag, FiAlignLeft, FiCheckSquare, FiX, FiFolder, FiBookmark } from 'react-icons/fi'
import { useBoard } from '../../context/BoardContext'
import { useAuth } from '../../context/AuthContext'
import { getProjects, getLabels } from '../../lib/api'
import type { Project, Label } from '../../types/database'
import Button from '../UI/Button'
import styles from './TaskModal.module.css'

interface SubtaskForm {
  id?: string
  title: string
  is_completed: boolean
}

export default function TaskModal() {
  const { state, closeModal, saveTask } = useBoard()
  const { user } = useAuth()
  const [title, setTitle] = useState('')
  const [description, setDescription] = useState('')
  const [priority, setPriority] = useState<'low' | 'medium' | 'high'>('medium')
  const [dueDate, setDueDate] = useState('')
  const [tags, setTags] = useState<string[]>([])
  const [tagInput, setTagInput] = useState('')
  const [subtasks, setSubtasks] = useState<SubtaskForm[]>([])
  const [subtaskInput, setSubtaskInput] = useState('')
  const [projectId, setProjectId] = useState<string | null>(null)
  const [selectedLabels, setSelectedLabels] = useState<string[]>([])
  const [projects, setProjects] = useState<Project[]>([])
  const [labels, setLabels] = useState<Label[]>([])
  const titleInputRef = useRef<HTMLInputElement>(null)

  const isEditing = !!state.editingTask

  // Load projects and labels
  useEffect(() => {
    const loadData = async () => {
      if (!user) return
      const [projectsRes, labelsRes] = await Promise.all([
        getProjects(user.id),
        getLabels(user.id)
      ])
      if (projectsRes.data) setProjects(projectsRes.data)
      if (labelsRes.data) setLabels(labelsRes.data)
    }
    loadData()
  }, [user])

  // Load task data when editing
  useEffect(() => {
    if (state.editingTask) {
      setTitle(state.editingTask.title)
      setDescription(state.editingTask.description || '')
      setPriority(state.editingTask.priority)
      setDueDate(state.editingTask.due_date || '')
      setTags(state.editingTask.tags?.map(t => t.name) || [])
      setSubtasks(state.editingTask.subtasks?.map(s => ({
        id: s.id,
        title: s.title,
        is_completed: s.is_completed
      })) || [])
      setProjectId(state.editingTask.project_id || null)
      setSelectedLabels(state.editingTask.task_labels?.map(tl => tl.label_id) || [])
    } else {
      resetForm()
    }
  }, [state.editingTask, state.isModalOpen])

  // Focus title input when modal opens
  useEffect(() => {
    if (state.isModalOpen && titleInputRef.current) {
      setTimeout(() => titleInputRef.current?.focus(), 100)
    }
  }, [state.isModalOpen])

  const resetForm = () => {
    setTitle('')
    setDescription('')
    setPriority('medium')
    setDueDate('')
    setTags([])
    setTagInput('')
    setSubtasks([])
    setSubtaskInput('')
    setProjectId(null)
    setSelectedLabels([])
  }

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault()

    if (!title.trim()) {
      return
    }

    await saveTask(
      {
        title: title.trim(),
        description: description.trim() || null,
        priority,
        due_date: dueDate || null
      },
      tags,
      subtasks.map(s => ({
        id: s.id,
        title: s.title,
        is_completed: s.is_completed
      })),
      selectedLabels,
      projectId
    )

    resetForm()
  }

  const handleClose = () => {
    closeModal()
    resetForm()
  }

  const handleOverlayClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      handleClose()
    }
  }

  // Tags handlers
  const handleTagKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      e.preventDefault()
      const tag = tagInput.trim().toLowerCase()
      if (tag && !tags.includes(tag)) {
        setTags([...tags, tag])
      }
      setTagInput('')
    }
  }

  const removeTag = (index: number) => {
    setTags(tags.filter((_, i) => i !== index))
  }

  // Subtasks handlers
  const handleSubtaskKeyDown = (e: KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter') {
      e.preventDefault()
      addSubtask()
    }
  }

  const addSubtask = () => {
    const subtaskTitle = subtaskInput.trim()
    if (subtaskTitle) {
      setSubtasks([...subtasks, { title: subtaskTitle, is_completed: false }])
      setSubtaskInput('')
    }
  }

  const toggleSubtask = (index: number) => {
    setSubtasks(subtasks.map((s, i) =>
      i === index ? { ...s, is_completed: !s.is_completed } : s
    ))
  }

  const removeSubtask = (index: number) => {
    setSubtasks(subtasks.filter((_, i) => i !== index))
  }

  // Label toggle
  const toggleLabel = (labelId: string) => {
    if (selectedLabels.includes(labelId)) {
      setSelectedLabels(selectedLabels.filter(id => id !== labelId))
    } else {
      setSelectedLabels([...selectedLabels, labelId])
    }
  }

  if (!state.isModalOpen) return null

  return (
    <div
      className={`${styles.modalOverlay} ${state.isModalOpen ? styles.active : ''}`}
      onClick={handleOverlayClick}
    >
      <div className={styles.modal}>
        <div className={styles.modalHeader}>
          <h3>
            {isEditing ? <FiEdit2 /> : <FiPlus />}
            {isEditing ? ' Edit Task' : ' Add New Task'}
          </h3>
          <button className={styles.modalClose} onClick={handleClose}>
            <FiX />
          </button>
        </div>

        <form onSubmit={handleSubmit}>
          <div className={styles.modalBody}>
            {/* Title & Priority */}
            <div className={styles.formRow}>
              <div className={`${styles.formGroup} ${styles.flex2}`}>
                <label>
                  <FiType />
                  Task Title
                </label>
                <input
                  ref={titleInputRef}
                  type="text"
                  value={title}
                  onChange={(e) => setTitle(e.target.value)}
                  placeholder="Enter task title..."
                  required
                />
              </div>
              <div className={styles.formGroup}>
                <label>
                  <FiFlag />
                  Priority
                </label>
                <select
                  value={priority}
                  onChange={(e) => setPriority(e.target.value as 'low' | 'medium' | 'high')}
                >
                  <option value="low">Low</option>
                  <option value="medium">Medium</option>
                  <option value="high">High</option>
                </select>
              </div>
            </div>

            {/* Project & Due Date */}
            <div className={styles.formRow}>
              <div className={styles.formGroup}>
                <label>
                  <FiFolder />
                  Project
                </label>
                <select
                  value={projectId || ''}
                  onChange={(e) => setProjectId(e.target.value || null)}
                >
                  <option value="">No Project</option>
                  {projects.map(project => (
                    <option key={project.id} value={project.id}>
                      {project.name}
                    </option>
                  ))}
                </select>
              </div>
              <div className={styles.formGroup}>
                <label>
                  <FiCalendar />
                  Due Date
                </label>
                <input
                  type="date"
                  value={dueDate}
                  onChange={(e) => setDueDate(e.target.value)}
                />
              </div>
            </div>

            {/* Labels */}
            <div className={styles.formGroup}>
              <label>
                <FiBookmark />
                Labels
              </label>
              <div className={styles.labelsContainer}>
                {labels.length === 0 ? (
                  <span className={styles.noLabels}>No labels yet. Create some in Labels page.</span>
                ) : (
                  <div className={styles.labelsList}>
                    {labels.map(label => (
                      <button
                        key={label.id}
                        type="button"
                        className={`${styles.labelBtn} ${selectedLabels.includes(label.id) ? styles.selected : ''}`}
                        style={{
                          background: selectedLabels.includes(label.id) ? label.color : `${label.color}20`,
                          color: selectedLabels.includes(label.id) ? 'white' : label.color,
                          borderColor: label.color
                        }}
                        onClick={() => toggleLabel(label.id)}
                      >
                        {label.name}
                      </button>
                    ))}
                  </div>
                )}
              </div>
            </div>

            {/* Tags */}
            <div className={styles.formRow}>
              <div className={`${styles.formGroup} ${styles.flex2}`}>
                <label>
                  <FiTag />
                  Tags
                </label>
                <div className={styles.tagsInputContainer}>
                  <div className={styles.tagsList}>
                    {tags.map((tag, i) => (
                      <span key={i} className={styles.tagItem}>
                        {tag}
                        <button type="button" onClick={() => removeTag(i)}>&times;</button>
                      </span>
                    ))}
                  </div>
                  <input
                    type="text"
                    className={styles.tagInput}
                    value={tagInput}
                    onChange={(e) => setTagInput(e.target.value)}
                    onKeyDown={handleTagKeyDown}
                    placeholder="Add tag..."
                  />
                </div>
              </div>
            </div>

            {/* Description */}
            <div className={styles.formGroup}>
              <label>
                <FiAlignLeft />
                Description
              </label>
              <textarea
                value={description}
                onChange={(e) => setDescription(e.target.value)}
                placeholder="Add more details..."
                rows={2}
              />
            </div>

            {/* Subtasks */}
            <div className={styles.formGroup}>
              <label>
                <FiCheckSquare />
                Subtasks
              </label>
              <div className={styles.subtasksContainer}>
                <div className={styles.subtasksList}>
                  {subtasks.map((subtask, i) => (
                    <div key={i} className={`${styles.subtaskItem} ${subtask.is_completed ? styles.completed : ''}`}>
                      <input
                        type="checkbox"
                        checked={subtask.is_completed}
                        onChange={() => toggleSubtask(i)}
                      />
                      <span>{subtask.title}</span>
                      <button type="button" onClick={() => removeSubtask(i)}>
                        <FiX />
                      </button>
                    </div>
                  ))}
                </div>
                <div className={styles.addSubtask}>
                  <input
                    type="text"
                    value={subtaskInput}
                    onChange={(e) => setSubtaskInput(e.target.value)}
                    onKeyDown={handleSubtaskKeyDown}
                    placeholder="Add subtask..."
                  />
                  <button type="button" onClick={addSubtask}>
                    <FiPlus />
                  </button>
                </div>
              </div>
            </div>
          </div>

          <div className={styles.modalFooter}>
            <Button type="button" variant="secondary" onClick={handleClose}>
              Cancel
            </Button>
            <Button type="submit" variant="primary">
              <FiPlus />
              {isEditing ? 'Save Changes' : 'Add Task'}
            </Button>
          </div>
        </form>
      </div>
    </div>
  )
}
